import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import vuetify from './plugins/vuetify';
import axios from 'axios';


Vue.config.productionTip = false

console.log('main JS');
import {usuarioP} from './test/usuario';

const userConected = async () => {
  const token = localStorage.getItem('iToken');
  if(!token) return null;
  /* const data = await axios.get('https://rickandmortyapi.com/api/character/1');
  if(!data) return null;
  return data.data; */

  /* Prueba */
  console.log(usuarioP);
  const data = usuarioP;
  if(!data) return null;
  return data;
}

const render = async () => {
  const usuario = await userConected();
  console.log('retorno del usuario', usuario);
  if(usuario === null) {
    localStorage.removeItem('iToken');
    router.push('/app1/login');
  } else {
    store.commit('setUsuarioPrueba', usuario);
    console.log(store.getters.user);
  }
  new Vue({
    store,
    router,
    vuetify,
    render: h => h(App)
  }).$mount('#app')

}

render();

